.crt-corners::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 20px;
  box-shadow: 
    inset 0 0 100px rgba(0, 0, 0, 0.5),
    inset 0 0 40px rgba(0, 0, 0, 0.4);
  pointer-events: none;
}

/* Animation spécifique pour la révélation des gages */

/* Dans index.css, ajoutez ces styles */
.reveal-animation {
  background-color: #330000 !important;
  color: #ff3333 !important;
  transform: scale(1.1) !important;
  transition: all 0.3s ease !important;
  border: 2px solid #ff3333 !important;
}

/* Si vous voulez toujours une animation, vous pouvez ajouter */
@keyframes redReveal {
  0% { 
    background-color: #000000 !important;
    color: #00ff00 !important;
    transform: scale(1) !important;
  }
  50% { 
    background-color: #330000 !important;
    color: #ff3333 !important;
    transform: scale(1.2) !important;
  }
  100% { 
    background-color: #220000 !important;
    color: #ff3333 !important;
    transform: scale(1.1) !important;
  }
}

.reveal-animation {
  animation: redReveal 0.6s ease-out forwards !important;
}

.reveal-animation {
  animation: redReveal 0.6s ease-out forwards;
}
@keyframes revealAnimation {
  0% { transform: scale(1); }
  20% { transform: scale(1.2); }
  40% { transform: scale(1.1); }
  60% { transform: scale(1.15); }
  80% { transform: scale(1.1); }
  100% { transform: scale(1.1); }
}

.reveal-animation {
  animation: revealAnimation 0.5s ease-out forwards;
}